.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 20;
    background-color: rgba(0, 0, 0, 0.75);
}

.modal {
    position: fixed;
    top: 40vh;
    left: 25%;
    width: 50%;
    background-color: white;
    padding: 1rem;
    border-radius: 14px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 30;
    animation: slide-down 300ms ease-out forwards;
}

.content p {
    color: #414141;
    margin-top: 2em !important;
    letter-spacing: 0em;
    font-weight: 600;
}
.content {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    cursor: pointer;
}

.icon-success {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.5em;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: #cdfadd;
}

.icon {
    display: flex;
    align-items: center;
}

@media (min-width: 768px) {
    .modal {
        width: 40rem;
        padding: 4em;
        left: calc(50% - 20rem);
    }
}

@keyframes slide-down {
    from {
        opacity: 0;
        transform: translateY(-3rem);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
